import { type IGestureEvent } from '@huangjs888/gesture';
import buttonPress from './buttonPress';
import type SlideView from '../slideview';
import { findTarget } from '../util';

export default function tap(this: SlideView, e: IGestureEvent) {
  const { element, contentEl, leftEl, rightEl, _translate } = this;
  const { sourceEvent } = e;
  const target = findTarget(
    sourceEvent,
    (t) => t !== element && t !== contentEl && t !== leftEl && t !== rightEl,
  );
  // 
  if (contentEl && target === contentEl) {
    // this._directionthis._translate
    // this._translatethis._direction
    // 
    if (_translate === 0) {
      this.emit('press', {
        currentTarget: contentEl,
        timestamp: Date.now(),
        sourceEvent: e,
      });
    } else {
      this.hide();
    }
  }
  // 
  else if (target === leftEl) {
    buttonPress.apply(this, [e, 'left']);
  }
  // 
  else if (target === rightEl) {
    buttonPress.apply(this, [e, 'right']);
  }
}
