import EventTarget from '@huangjs888/gesture/emitter';
import type { IGestureEvent } from '@huangjs888/gesture';
import { type IElement, getElement, createElement, cssInject } from '@huangjs888/lightdom';
import { bindGesture, onOnceTransitionEnd } from './events';
import { transform } from './transform';
import { overshootChange } from './overshoot';
import { confirmStyle, confirmCancel } from './confirm';
import { debounce, getIconType } from './util';
import css from './css';

cssInject('hjs-slide-view-style', css);

class SlideView extends EventTarget<IType, IEvent> {
  element: HTMLElement | null = null; // 
  contentEl: HTMLElement | null = null; // 
  leftEl: HTMLElement | null = null; // 
  rightEl: HTMLElement | null = null; // 
  leftActions: IMergeAction | null = null; // 
  rightActions: IMergeAction | null = null; // 
  friction: number = 0.5; // (0-1)
  rebounce: number = 12; // 
  duration: number = 0.4; // 
  timing: ITiming = 'ease'; // 
  _destory: boolean = false; // 
  _direction: IDirection = 'none'; // 
  _confirming: IConfirm | null = null; // 
  _overshooting: boolean = false; // overshoot
  _translate: number = 0; // 
  _width: number = 0; // 
  _offset: number = 0; // offsetLeft
  _startOffset: number = 0; // translate
  _startTranslate: number = 0; // translaterebounceSize
  _startPoint: number[] | null = null; // 
  _startAngle: number = 0; // 45
  _timestamp: number = 0; // 
  _isMoving: boolean = false; // 
  _unbind: (() => void) | null;

  constructor(options: IOption) {
    super();
    const {
      className,
      container,
      content,
      friction,
      rebounce,
      duration,
      timing,
      leftActions,
      rightActions,
    } = options;
    const tempContainer = getElement(container);
    if (!tempContainer) {
      throw new Error('Please pass in a valid container element...');
    }
    const element = (this.element = createElement(
      {
        className: ['hjs-slideview', className || ''],
      },
      [
        (this.leftEl = createElement({
          className: 'hjs-slideview__left',
        }) as HTMLElement),
        (this.rightEl = createElement({
          className: 'hjs-slideview__right',
        }) as HTMLElement),
        (this.contentEl = createElement({
          className: 'hjs-slideview__content',
        }) as HTMLElement),
      ],
      tempContainer,
    ) as HTMLElement);
    const gesture = bindGesture.apply(this, [element]);
    this.setContent(content);
    this.setFriction(friction);
    this.setRebounce(rebounce);
    this.setDuration(duration);
    this.setTiming(timing);
    this.setActions(leftActions, 'left');
    this.setActions(rightActions, 'right');
    // 
    const resize = debounce(() => {
      const { width, left } = element.getBoundingClientRect();
      this._width = width;
      this._offset = left;
      this.setActions(this.leftActions || {}, 'left');
      this.setActions(this.rightActions || {}, 'right');
    }, 300);
    window.addEventListener('resize', resize);
    this._unbind = () => {
      gesture.destory();
      window.removeEventListener('resize', resize);
    };
    resize();
  }
  setContent(content: IElement, dangerous?: boolean) {
    if (this._destory || !this.contentEl) {
      return;
    }
    // XSS
    if (dangerous && typeof content === 'string') {
      this.contentEl.innerHTML = content;
      return;
    }
    const tempChild = getElement(content);
    if (tempChild) {
      this.contentEl.innerHTML = '';
      this.contentEl.appendChild(tempChild);
    }
  }
  setFriction(friction: number = 0.5) {
    if (this._destory) {
      return;
    }
    // friction: 0.50011
    if (typeof friction === 'number') {
      this.friction = Math.min(1, Math.max(0, friction));
    }
  }
  setRebounce(rebounce: number = 12) {
    if (this._destory) {
      return;
    }
    // rebounce: 1200
    if (typeof rebounce === 'number') {
      this.rebounce = Math.max(0, rebounce);
    }
  }
  setDuration(duration: number = 0.4) {
    if (this._destory) {
      return;
    }
    // duration: 0.400
    if (typeof duration === 'number') {
      this.duration = Math.max(0, duration);
    }
  }
  setTiming(timing: ITiming = 'ease') {
    if (this._destory) {
      return;
    }
    // timing: ease
    this.timing = timing;
  }
  setDisable(disable: boolean = true, direction: IDirection = 'both') {
    if (this._destory) {
      return;
    }
    // disable: true
    if (typeof disable === 'boolean') {
      this.hide().then(() => {
        // direction
        if (this.leftActions && (direction === 'both' || direction === 'left')) {
          this.leftActions.disable = disable;
        }
        if (this.rightActions && (direction === 'both' || direction === 'right')) {
          this.rightActions.disable = disable;
        }
      });
    }
  }
  setOvershoot(overshoot: boolean = true, direction: IDirection = 'both') {
    if (this._destory) {
      return;
    }
    // overshoot: true
    if (typeof overshoot === 'boolean') {
      // direction
      if (this.leftActions && (direction === 'both' || direction === 'left')) {
        this.leftActions.overshoot = overshoot;
      }
      if (this.rightActions && (direction === 'both' || direction === 'right')) {
        this.rightActions.overshoot = overshoot;
      }
    }
  }
  setThreshold(threshold: number = 40, direction: IDirection = 'both') {
    if (this._destory) {
      return;
    }
    // overshoot: 4000
    if (typeof threshold === 'number') {
      const _threshold = Math.max(0, threshold);
      // direction
      if (this.leftActions && (direction === 'both' || direction === 'left')) {
        this.leftActions.threshold = Math.min(_threshold, this.leftActions.width);
      }
      if (this.rightActions && (direction === 'both' || direction === 'right')) {
        this.rightActions.threshold = Math.min(_threshold, this.rightActions.width);
      }
    }
  }
  setActions(actions: IActionOption | IMergeAction = {}, direction: IDirection = 'both') {
    if (this._destory || direction === 'none') {
      return;
    }
    const _setActions = (_direction: 'left' | 'right') => {
      const parentEl = this[`${_direction}El`];
      if (actions && actions.items && actions.items.length > 0) {
        const {
          style = 'accordion',
          disable = false,
          overshoot = false,
          overshootEdgeSize = 80,
          overshootFreeSize = 30,
          threshold = 40,
          items,
        } = actions;
        let tElement = (actions as IMergeAction).element;
        if (!tElement) {
          parentEl && (parentEl.innerHTML = '');
          tElement = createElement(
            {
              className: ['hjs-slideview__actions', (actions as IActionOption).className || ''],
            },
            null,
            parentEl,
          ) as HTMLElement;
        }
        let tWidth = 0;
        let tGap = 0;
        const newItems = items.map((item, index) => {
          let element = (item as IMergeActionItem).element;
          let wrapper = (item as IMergeActionItem).wrapper;
          if (!element) {
            const { text, icon } = item as IActionItem;
            element = createElement(
              {
                className: 'hjs-slideview__action',
                attrs: {
                  dataIndex: String(index),
                },
              },
              [
                icon
                  ? createElement(getIconType(icon), {
                      className: 'hjs-slideview__action__icon',
                    })
                  : null,
                text
                  ? createElement('span', {
                      className: 'hjs-slideview__action__text',
                    })
                  : null,
              ],
            ) as HTMLElement;
            wrapper = createElement(
              {
                className: 'hjs-slideview__action__wrapper',
              },
              element,
              tElement,
            ) as HTMLElement;
            // 
            confirmStyle({
              ...item,
              wrapper,
              element,
              width: 0,
              gap: [0, 0],
              fixedGap: false,
            });
          }
          const { gap = 0, fixedGap = false } = item;
          const { width } = element.getBoundingClientRect();
          const leftGap = Math.min(width, Math.max(typeof gap === 'number' ? gap : gap[0], 0));
          const rightGap = Math.min(width, Math.max(typeof gap === 'number' ? gap : gap[1], 0));
          tWidth += width + leftGap + rightGap;
          tGap += leftGap + rightGap;
          return {
            ...item,
            wrapper,
            element,
            width,
            gap: [leftGap, rightGap],
            fixedGap: leftGap === 0 && rightGap === 0 ? false : fixedGap, // gap0gap
          };
        });
        this[`${_direction}Actions`] = {
          style,
          disable,
          overshoot,
          overshootFreeSize,
          overshootEdgeSize,
          threshold: Math.min(tWidth, Math.max(threshold, 0)),
          element: tElement,
          width: tWidth,
          gap: tGap,
          items: newItems,
        };
      } else {
        parentEl && (parentEl.innerHTML = '');
        this[`${_direction}Actions`] = null;
      }
    };
    const _setActionsAfterCollapse = (_direction: 'left' | 'right') => {
      // 
      this.hide().then(() => {
        _setActions(_direction);
        this.show(_direction);
      });
    };
    const shown = this._translate > 0 ? 'left' : this._translate < 0 ? 'right' : 'none';
    if (direction === 'both') {
      if (shown !== 'none') {
        _setActionsAfterCollapse(shown);
      }
      if (shown !== 'left') {
        _setActions('left');
      }
      if (shown !== 'right') {
        _setActions('right');
      }
    } else {
      if (shown === direction) {
        _setActionsAfterCollapse(direction);
      } else {
        _setActions(direction);
      }
    }
  }
  toggle(direction: IDirection = 'right') {
    return this._translate === 0 ? this.show(direction) : this.hide();
  }
  show(direction: IDirection = 'right') {
    const { contentEl, rebounce, leftActions, rightActions } = this;
    if (
      this._destory ||
      !contentEl ||
      ((!leftActions || leftActions.disable) && (!rightActions || rightActions.disable))
    ) {
      return Promise.resolve();
    }
    return new Promise<void>((resolve) => {
      let __direction = direction;
      if (!leftActions || leftActions.disable) {
        __direction = 'right';
      }
      if (!rightActions || rightActions.disable) {
        __direction = 'left';
      }
      const actions = __direction === 'left' ? leftActions : rightActions;
      const factor = __direction === 'left' ? 1 : -1;
      const maxTranslate = !actions ? 0 : actions.width * factor;
      if (this._translate === maxTranslate) {
        this.emit('show', {
          direction: __direction,
          currentTarget: contentEl,
          timestamp: Date.now(),
          sourceEvent: null,
        });
        this._direction = __direction;
        resolve();
        return;
      }
      const show = (rebSize: number = 0) => {
        const translate = maxTranslate + rebSize;
        this._translate = translate;
        transform.apply(this, [translate]);
        if (this._overshooting) {
          this._overshooting = false;
          overshootChange.apply(this, [this._translate > 0 ? leftActions : rightActions]);
        }
        confirmCancel.apply(this, []);
        if (!rebSize) {
          onOnceTransitionEnd(contentEl, () => {
            resolve();
            this.emit('show', {
              direction: __direction,
              currentTarget: contentEl,
              timestamp: Date.now(),
              sourceEvent: null,
            });
            this._direction = __direction;
          });
        }
      };
      // 
      if (
        rebounce > 0 &&
        ((maxTranslate > 0 && this._translate < maxTranslate) ||
          (maxTranslate < 0 && this._translate > maxTranslate))
      ) {
        onOnceTransitionEnd(contentEl, () => show());
        show(rebounce * factor);
      } else {
        show();
      }
    });
  }
  hide() {
    const { contentEl, leftActions, rightActions } = this;
    if (
      this._destory ||
      !contentEl ||
      ((!leftActions || leftActions.disable) && (!rightActions || rightActions.disable))
    ) {
      return Promise.resolve();
    }
    return new Promise<void>((resolve) => {
      if (this._translate === 0) {
        this.emit('hide', {
          direction: 'none',
          currentTarget: contentEl,
          timestamp: Date.now(),
          sourceEvent: null,
        });
        this._direction = 'none';
        resolve();
        return;
      }
      this._translate = 0;
      transform.apply(this, [0]);
      // canceltransition
      onOnceTransitionEnd(contentEl, () => {
        resolve();
        if (this._overshooting) {
          this._overshooting = false;
          overshootChange.apply(this, [this._translate > 0 ? leftActions : rightActions]);
        }
        confirmCancel.apply(this, []);
        this.emit('hide', {
          direction: 'none',
          currentTarget: contentEl,
          timestamp: Date.now(),
          sourceEvent: null,
        });
        this._direction = 'none';
      });
    });
  }
  destory() {
    // 
    super.off();
    // 
    if (this._unbind) {
      this._unbind();
      this._unbind = null;
    }
    this._destory = true;
    this.leftActions = null;
    this.rightActions = null;
    this._confirming = null;
    this._startPoint = null;
    this.contentEl = null;
    this.leftEl = null;
    this.rightEl = null;
    if (this.element) {
      // 
      if (this.element.parentNode) {
        this.element.parentNode.removeChild(this.element);
      }
      this.element = null;
    }
  }
}

export type IDirection = 'left' | 'right' | 'both' | 'none';

export type ITiming =
  | 'linear'
  | 'ease'
  | 'ease-in'
  | 'ease-out'
  | 'ease-in-out'
  | `cubic-bezier(${number},${number},${number},${number})`;

export type IActionStyle = 'drawer' | 'accordion';

export type IConfirm = {
  direction: IDirection;
  index: number;
};

export type IMergeActionItem = {
  wrapper: HTMLElement; // 
  element: HTMLElement; // 
  width: number; // 
  gap: number[]; // 
  fixedGap: boolean; //
} & IActionItem;

export type IMergeAction = {
  style: IActionStyle; // 
  disable: boolean; // 
  threshold: number; // 
  overshoot: boolean; // ()
  overshootFreeSize: number; // overshoot
  overshootEdgeSize: number; // overshoot
  element: HTMLElement; // 
  width: number; // 
  gap: number; // 
  items: IMergeActionItem[]; // 
};

export type IActionItem = {
  className?: string; // 
  text?: string; // 
  icon?: string; // imgsvgicon
  color?: string; // img
  background?: string; //
  confirm?: {
    // {}
    className?: string; // 
    text?: string; // 
    icon?: string; // 
    color?: string; // 
    background?: string; //
  }; // 
  collapse?: boolean; // 
  gap?: number | number[]; // 
  fixedGap?: boolean; //
  data?: any; //
};

export type IActionOption = {
  className?: string; // 
  style?: IActionStyle; // 
  disable?: boolean; // 
  threshold?: number; // 
  overshoot?: boolean; // ()
  overshootEdgeSize?: number; // overshoot
  overshootFreeSize?: number; // overshoot
  items?: IActionItem[]; // 
};

export type IOption = {
  className?: string; // 
  container: IElement; // DOM
  content?: IElement; // 
  friction?: number; // (0-1)
  rebounce?: number; // 
  duration?: number; // 
  timing?: ITiming; // 
  leftActions?: IActionOption;
  rightActions?: IActionOption;
};

export type IType =
  | 'show'
  | 'hide'
  | 'press'
  | 'longPress'
  | 'doublePress'
  | 'buttonPress'
  | 'buttonConfirm'
  | 'transform';

export type IEvent = {
  currentTarget: HTMLElement;
  timestamp: number;
  sourceEvent: IGestureEvent | null;
  direction?: IDirection; // 
  index?: number; // 
  data?: any; // 
};

export default SlideView;
